@import 'variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  background-color: white;
  position: sticky;
  top: 0;
  z-index: 1;
  @media (max-width: 736px){
    .container{
      flex-direction: column;
    }
    .header__cart{
      padding-right: 40px;
    }
  }


  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;


  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
