@import 'fonts';
@import 'variables';
@import 'libs/normalize';

@import 'components/all';

body {
  background-color:#FFC373 ;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;
  @media (max-width: 645px){
    width: calc(100vw - 30px);
    margin-top: 15px;
  }
}

.content {
  padding: 40px 0;
  display: flex;
  &__error-info{
    margin: 100px auto;
    width: 500px;
    text-align: center;
    h2{
      font-size: 28px;
      font-weight: 700;
      margin-bottom: 10px;
    }
    p{
      font-size: 20px;
    }
  }

  &__title {
    margin-top: 60px;
    margin-bottom: 50px;
    font-weight: 800;
    font-size: 42px;
    @media(max-width: 1060px){
      text-align: center;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-column-gap: 20px;

    @media (max-width: 1350px){
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 1060px){
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 730px){
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1250px){
      flex-direction: column;
      .categories{
        margin-bottom: 30px;
        
      }
    }
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 900px;
    
    margin: 60px auto;
    .content__title {
      margin: 0;
    }
  }
}

.cart {
  
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .content__items{
    display: block;
    padding: 0;
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;
    transition: 0.25s;
   
    &--name{
      display: flex;
      width: 100%;

      @media (max-width: 750px){
        flex-direction: column;
        
      }
    }

    &--utils{
      display: flex;
      justify-content: center;
      width: 100%;
      @media (max-width: 939px){
        display:block;
      }
      
    }
    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      width: 10%;
      transition: 0.25s;
      
      img {
        width: 90px;
        height: 90px;
        @media (max-width: 750px){
          width: 110px;
          height: 110px;
          margin-left: 15px;
          
        }
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      
      width: 50%;
      

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
        margin-left: 40px;
        @media (max-width: 750px){
          margin-left: 5px;
          font-size: 23px;
          width: 200%;
        }
      }

      p {
        margin-left: 40px;
        @media (max-width: 750px){
          margin-left: 5px;
          font-size: 23px;
          width: 170%;
        }
        font-size: 18px;
        color: #8d8d8d;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100px;
      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
        @media (max-width: 750px){
          font-size: 25px;
        }
      }
      @media (max-width: 750px){
        height: 100px;
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: #FFC373;
      border-radius: 16px;
      margin-top: 28px;
      height: 35px;
      width: 100px;
      @media (max-width: 750px){
        margin-top: 40px;
        
      }
      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }

        }
       
      }

      b {
        transition: 0.25s;
        font-size: 25px;
        @media (max-width: 750px){
          font-size: 28px;
          
        }
      }
      
    }

    

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 50px;
      
      .button {
        border-color: darken($color: $gray-line, $amount: 10);
        
      }

      svg {
        transform: rotate(45deg);
        
        path {
          fill: darken($color: $gray-line, $amount: 15);
          
        }
        
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
          @media (max-width: 750px){
            width: 25.5px;
            height: 25.5px;
            
          }
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        @media (max-width: 750px){
          width: 160px;
          font-size: 16px;
          font-weight: 600;
          padding: 16px;
        }
        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
        @media (max-width: 750px){
          width: 160px;
        }
      }
    }
  }

  &--empty {
    margin: 0 auto;
    width: 560px;
    text-align: center;
    h2 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 310px;
      margin: 45px auto 60px;
      @media (max-width: 348px){
        width: 250px;
      }
      @media (max-width: 284px){
        width: 150px;
      }
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}
